//字體來源管理設定
    // 字體樣式
        //Silom 字體

        @font-face {
            font-family: 'Silom';
            src: local("Silom");
            src: url('../fonts/Silom.ttf') format("opentype");
        }
        .fy-silom{
            font-family: 'Silom';
        }

        //思源黑體
        // @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
        @font-face {
            font-family: 'Noto Sans TC', sans-serif;
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Black.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Bold.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Light.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Medium.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Regular.otf') format("truetype");
            src: url('../fonts/Noto_Sans_TC/NotoSansTC-Thin.otf') format("truetype");

        }
        
        .fy-noto {
            font-family: 'Noto Sans TC', sans-serif;
        // ---------
    // ---------字體樣式 結束
        }
// --------------------字體設定區結束


// -----變數設定-----
    // 路徑設定
    $bgPath:'../images/';
    $bgPath2: '../images/home/';
    //路徑設定結束
    // 圖檔
    $con5-bg1:'eggings_bending_her_body.jpg';
    $con5-bg2:'woman_performing_yoga.jpg';
    $con5-bg3:'woman_in_blue_leggings_and_black_tank_top_doing_yoga.jpg';
    $con6-img1:'Ellipse_1@2x.png';
    $con6-img2:'Ellipse1_2@2x.png';
    $con6-img3:'Ellipse1_4@2x.png';
    $con6-img4:'Ellipse1_1@2x.png';
    $con6-img5:'Ellipse1_3@2x.png';
    $con6-img6:'Ellipse1_5@2x.png';
    // 圖檔設定結束

// -----變數設定結束-----



// 容器斷點設定
        // PC 
        @mixin pc {
            @media(min-width: 1200px){
                @content;
            }
        }
        // Table
        @mixin table {
            @media (min-width: 768px){
                @content;
            }
        }
    // ------------
    


        //logo 文字轉圖片
        .navbar-brand {
            display: block;
            width: 96px;
            height: 31px;
            background-image: url('../images/logo.png');
            text-indent: 101%;
            overflow: hidden;
            word-wrap: nowrap;
            background-size: contain;  
            background-repeat: no-repeat;
            background-position: center center;       
        }

        // toggle ico圖示更換
        .toggler-img {
            display: block;
            width: 33px;
            height: 25.67px;
            background-image: url('../images/icon.png');
            background-size: contain;  
            background-repeat: no-repeat;
            background-position: center center; 
            
        }


        // 外觀
            // 顏色
            $turkish-rose: #907A7B;
            .text-rose {
                color: $turkish-rose !important;
            }
            .bg-rose {
                background-color: $turkish-rose !important;
            }
            // ---------------
            // 圓角設定
            .rounded-sp {
                border-radius: 16px;
            }
            .rounded-top-sp {
                border-top-left-radius: 16px;
                border-top-right-radius: 16px;
            }
            // ---------------------
            //按鈕設定


            // ---------------------
            // hover等效設定
            .hover-sp {
                &:hover {
                    background-color: $turkish-rose;
                    color: #F5F0E7;
                }
            }

            .hover-sp2 {
                    color: #707070;
                &:hover {
                    background-color: #C0A4A5;
                    border-color: #C0A4A5;
                    color: #fff;
                }
            }

            .hover-sp3 {
                color: #fff;
                &:hover {
                    background-color: $turkish-rose;
                    border-color: $turkish-rose;
                    color: #fff;
                }
            }
            // ---------------------
            // header背景
            .header {
                background-image: url('../images/photo-1612157777902-5382bc6e864b.jpeg');
                background-repeat: no-repeat;
                background-size: cover;
                background-position: 50% bottom;
            }
            
            // ---------------------
            //content1 area
