                background-image: url('../images/photo-1593810450967-f9c42742e326.jpeg');
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center 0;
                padding-top: 314px !important;
                @include table {
                    padding-top: 136px !important;
                }
                @include pc {
                    padding-top: 314px !important;
                }
            }
            .c-img-2 {
                background-image: url('../images/photo-1593810451137-5dc55105dace.jpeg');
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center 0;
                padding-top: 314px !important;
                @include table {
