                }
            } 
            /* content1 finishi */


            // content2 area
            .con2-photo1 {
                min-width: 540px;
                min-height: 628px;
                background-image: url('../images/photo-1540206276207-3af25c08abc4.jpeg');
                background-size: cover;
                background-repeat: no-repeat;
                background-position: 0 center;
                
            }
                .con2 >.a-side {
                    margin-left: 0px;
                    @include table {
                        margin-left: 120px;
                    }
                    @include pc {
                        margin-left: 0px;
                    }
                }
                .teachers-card img {
                    width: 48px;
                    height: 48px;
                    @include table {
                        width: 68px;
                        height: 68px;
                    }
                }
                // teachers動畫特效
                .teachers {
                    position: relative;
                
                    // vertical-align: middle;
                    // transform: translateX(0);
                    backface-visibility: hidden;
                    -moz-osx-font-smoothing: grayscale;
                    transition-duration: 0.1s;
                    transition-property: transform;
                    &:hover,:active,:focus {
                        transform: scale(1.05);
                
                    }
                    @include table{
                        &:hover,:active,:focus {
                            transform: scale(1.176);
                            right: 8.5%;
                        
                        }
                    }
                    @include pc {
                        &:hover,:active,:focus {
                            transform: scale(1.176);
                            right: 8.5%;
                            
                        }
                    }
                }
                // teachers動畫特效finish

            // content2 finish
            // content3 area
                .con3 {
                    .bg-1 {
                        background-image: url('../images/carl-barcelo-nqUHQkuVj3c-unsplash.jpg');
                        min-width: 165px;
                        min-height: 210px;
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: 46% 0;
                        @include table {
                            min-width: 210px;
