                        }
                        @include pc{
                            min-width: 350px;
                            min-height: 444px;
                        }
                    }
                    .bg-2 {
                        background-image: url('../images/white_round_table_near_brown_woode_chair.jpg');
                        min-width: 165px;
                        min-height: 210px;
