                        }
                    }
                }
            // content3 finish
            // content4
                .con4-bg {
                    // background-image: url('../images/woman_doing_yoga_beside_wall.jpg');
                    width: 165px;
                    height: 165px;
                    background-size: cover ;
                    background-repeat: no-repeat;
                    
                    @include table {
                
                        width: 150px;
                        height: 150px;
                    }
                    @include pc {
                        width: 445px;
                        height: 445px;
                    }
                }
                
                .con4 {
                    position: relative;
                    .bg-1 {
                        background-image: url('../images/woman_doing_yoga_beside_wall.jpg');
                        background-position: center bottom;
                    }
                    .bg-2 {
                        background-image: url('../images/rawan-yasser-zQ1ypq-WHzQ-unsplash.jpg');
