            // content4 finish

            // content5
                .cardFace {  
                    min-height: 248.5px;
                    background-size: cover;
                    background-repeat: no-repeat;
                    border-top-left-radius: 16px;
                    border-top-right-radius: 16px;
                    background-position: center center;
                    @include table {
                        min-height: 238px;
                        border-top-right-radius: 0px;
                        border-bottom-left-radius: 16px;
                    }
                    @include pc {
                        height: 248.5px;
                        border-bottom-left-radius: 0px;
                        border-top-right-radius: 16px;
                    }
                }
                .con5-bg1 {                   
                    background-image: url($bgPath+$con5-bg1);
                    
                }
                .con5-bg2 {                   
                    background-image: url($bgPath+$con5-bg2);
                    
                }
                .con5-bg3 {                   
                    background-image: url($bgPath+$con5-bg3);
                    
                }
                .con5 a {
                    &:hover {
                        color: #000 !important;
                    }
                }
            // content5 finish
            // content6
            .personCard {    
                max-width: 345px;
                height: 116px;
